import React from 'react';
import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUndo } from '@fortawesome/free-solid-svg-icons'
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

interface Game {
  id: number;
  misses: number;
  cups: number[];
  lastAction: number;
}

interface GameState{
  currentGame: Game;
  hittedCups: number[];
  games: Game[];
}

interface GameProps{

}

class App extends React.Component<GameProps, GameState> {
  constructor(props: GameProps) {
    super(props);
    this.state = {
      currentGame: {
        id: Math.random(),
        misses: 0,
        lastAction: Date.now(),
        cups: Array(10).fill(0),
      },
      hittedCups: [],
      games: [],
    };
  }
  
  handleUndo(){
    const hittedCups = this.state.hittedCups.slice();
    if(hittedCups.length > 0){
      const lastHit = hittedCups.pop();
      if(lastHit === -1){
        this.setState({currentGame: {
          id: this.state.currentGame.id,
          misses: this.state.currentGame.misses - 1,
          cups: this.state.currentGame.cups,
          lastAction: this.state.currentGame.lastAction
          },hittedCups: hittedCups
          })
      }else if(lastHit != undefined){
        const cups = this.state.currentGame.cups.slice();
        cups[lastHit] = cups[lastHit] - 1
        this.setState({currentGame: {
          id: this.state.currentGame.id,
          misses: this.state.currentGame.misses,
          lastAction: this.state.currentGame.lastAction,
          cups: cups,
          },hittedCups: hittedCups})
      }
    }
  }

  handleMiss(){
    const hittedCups = this.state.hittedCups.slice();
    hittedCups.push(-1);
    const misses = this.state.currentGame.misses + 1;
    this.setState({currentGame: {
      id: this.state.currentGame.id,
      misses: misses,
      cups: this.state.currentGame.cups,
      lastAction: Date.now(),
    },
    hittedCups: hittedCups});
  }

  handleClick(i: number) {
    const hittedCups = this.state.hittedCups.slice();
    hittedCups.push(i);
    const cups = this.state.currentGame.cups.slice();
    cups[i] =  this.state.currentGame.cups[i]+1;
    this.setState({currentGame: {
      id: this.state.currentGame.id,
      cups: cups,
      misses: this.state.currentGame.misses,
      lastAction: Date.now(),
    },
    hittedCups: hittedCups,});
  }

  newGame(){
    const games = this.state.games.slice();
    games.push({
      id: Math.random(),
      lastAction: this.state.currentGame.lastAction,
      misses: this.state.currentGame.misses, 
      cups: this.state.currentGame.cups,
    });
    games.sort(function (a, b) {
      return b.lastAction - a.lastAction;
    });
    this.setState({currentGame: {
      id: this.state.currentGame.id,
      misses: 0,
      cups: Array(10).fill(0),
      lastAction: Date.now(),
    }, 
    hittedCups: [],
    games: games,});
  }

  resumeGame(game: Game){
    const games = this.state.games.slice();
    //Remove game from games list:
    games.filter((item)=>{
      return item.id != game.id;
    });
    games.push({
      id: this.state.currentGame.id,
      lastAction: this.state.currentGame.lastAction,
      misses: this.state.currentGame.misses, 
      cups: this.state.currentGame.cups,
    });
    games.sort(function (a, b) {
      return b.lastAction - a.lastAction;
    });
    //Set Game to currentGame:
    this.setState({currentGame: {
      id: game.id,
      misses: game.misses,
      cups: game.cups,
      lastAction: game.lastAction
    },
    hittedCups: [],
    games: games, });
  }

  render() {
    return (
      <Container>
        <Row className="justify-content-left">
          <Button onClick={() => this.newGame()}>New Game</Button></Row>
          <Board game={this.state.currentGame}
            handleClick={(i) => this.handleClick(i)}
            handleMiss={() => this.handleMiss()}
            handleUndo={() => this.handleUndo()}
          />
          <LastGames games={this.state.games} onResume={(game) => this.resumeGame(game)}/>
        </Container>
    );
  }
}

interface BoardProps{
  handleMiss: ()=>void;
  handleClick: (cup: number)=>void;
  handleUndo: ()=>void;
  game: Game;
}

class Board extends React.Component<BoardProps, {}> {
  calcHitrate(cups: number[], misses: number){
    const accumulation = (accumulator: number, currentValue: number) => accumulator + currentValue;
    if(cups.reduce(accumulation) + misses > 0){
      return Math.round((cups.reduce(accumulation)/(cups.reduce(accumulation) + misses))*100)
    }else{
      return 0;
    }
  }

  render() {
    return (
      <Container>
        <Row className="justify-content-center">
          <Button variant="outline-primary" size="lg" className="btn-miss"
          onClick={() => this.props.handleMiss()}
          >Misses: {this.props.game.misses}</Button>
          <Button variant="outline-primary" size="lg" className="btn-undo"
          onClick={() => this.props.handleUndo()}
          ><FontAwesomeIcon icon={faUndo} /></Button>
        </Row>
        <Cups mini={false} cups={this.props.game.cups} handleClick={(i) => this.props.handleClick(i)} />
        <Row className="justify-content-center">
          Hitrate: {this.calcHitrate(this.props.game.cups, this.props.game.misses)}%
        </Row>
        </Container>
    );
  }
}

interface CupsProps{
  mini: boolean,
  cups: number[],
  handleClick: (cup: number)=>void
}

class Cups extends React.Component<CupsProps,{}>{
  renderCup(i: number) {
    return (<Cup mini={this.props.mini} value={this.props.cups[i]} onClick={() => this.props.handleClick(i)} />);
  }

  render() {return(
      <Container>
        <Row className="justify-content-center">
          {this.renderCup(0)}
          {this.renderCup(1)}
          {this.renderCup(2)}
          {this.renderCup(3)}
        </Row>
        <Row className="justify-content-center">
          {this.renderCup(4)}
          {this.renderCup(5)}
          {this.renderCup(6)}
        </Row>
        <Row className="justify-content-center">
          {this.renderCup(7)}
          {this.renderCup(8)}
        </Row>
        <Row className="justify-content-center">
          {this.renderCup(9)}
        </Row>
      </Container>);
    }   
}

interface CupProps{
  mini: boolean,
  value: number,
  onClick: ()=>void
}

class Cup extends React.Component<CupProps, {}> {
  render() {
    if(this.props.mini){
      return (
        <Button variant="outline-danger" className="minicup">
        {this.props.value}
        </Button>
      );
    }else{
      return (
        <Button variant="outline-danger" size="lg" className="cup"
        onClick={() => this.props.onClick()}>
        {this.props.value}
        </Button>
      );
    }
  }
}

interface LastGamesProps{
  onResume: (game: Game)=>void;
  games: Game[];
}

class LastGames extends React.Component<LastGamesProps, {}> {
  render() {
    var onResume = this.props.onResume;
    return (
      <Container>
        {this.props.games.map(function(game, i){
          const accumulation = (accumulator: number, currentValue: number) => accumulator + currentValue;
          const calcHitrate = function(cups: number[], misses: number){
            if(cups.reduce(accumulation) + misses > 0){
              return Math.round((cups.reduce(accumulation)/(cups.reduce(accumulation) + misses))*100)
            }else{
              return 0;
            }
          }
          return (
            <Row key={i}>
            <Col sm><Cups mini={true} cups={game.cups} handleClick={()=>{}} /></Col>
            <Col sm>
              <Row>Throws: {game.cups.reduce(accumulation)+game.misses}</Row>
              <Row>Hitrate: {calcHitrate(game.cups, game.misses)}%</Row>
            </Col>
            <Col sm><Button variant="secondary" onClick={() => onResume(game)}>Resume Game</Button></Col>
            <Col sm><Button variant="danger">Delete Game</Button></Col>
          </Row>);
        })}
      </Container>
    );
  }
}

export default App;
